import * as React from 'react';
import {
  AppBar, Box, Toolbar,
  Container, Button,
} from '@mui/material';
import logo from '../../assets/images/logoSVG.svg';

import { goToPage, openInNewTab } from '../../routes/route-helpers';
import { routeNaming } from '../../routes/routes';
import styled from '@emotion/styled';

const pages = [
  { name: 'About PPI', route: routeNaming.HOME },
  { name: 'Contact info', route: routeNaming.CONTACT_INFO},
];

const Logo = styled.img`
  cursor: pointer;
  width: 90px;

  @media (min-width: 500px) {
    width: 120px;
  }
`;

const Navbar = () => {
  return (
    <AppBar
      position="static"
      color="transparent"
      sx={{
        boxShadow: '0px 4px 8px rgba(234, 239, 244, 0.64)',
        paddingY: (theme) => theme.spacing(1)
      }}
    >
      <Container maxWidth="xl">
        <Toolbar disableGutters>
          <Logo
            src={logo}
            alt='The Alan Turing Institute'
            onClick={() => openInNewTab(routeNaming.ALAN_TURING)}
          />
          <Box sx={{
            marginLeft: 'auto'
          }}>
            {pages.map((page) => (
              <Button
                key={page.route}
                sx={{
                  marginLeft: (theme) => theme.spacing(4)
                }}
                onClick={() => goToPage(page.route)}
              >
                {page.name}
              </Button>
            ))}
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
};
export { Navbar };
